<script>

import Navbar from './Navbar.vue'

export default {
  name: 'Projects',
  components: {
    Navbar
  },
  props: {
    
  },
  data() {
    return {
      mobile:window.innerWidth <= 700,
      isShark: false,
      isChmiel: false,
      isHolmes: false,
      isImaginature: false
    }
  },
  mounted() {
    window.addEventListener('resize', () => {
      this.mobile = window.innerWidth <=1099;
      if (this.mobile) {
        this.isShark = true;
        this.isChmiel = true;
        this.isHolmes = true;
      } else {
        this.isShark = false;
        this.isChmiel = false;
        this.isHolmes = false;
      }
    })
    
  }
 
}
</script>

